.interview {
  background-color: #262D33;
  padding: 60px 0;
  @include mobile {
    padding: 30px 0;
  }
  &__container {
    row-gap: var(--offset);
    grid-template-rows: repeat(3, 170px);

    @include mobile {
      grid-template-columns: repeat(3, 1fr);
    }
  }
  &__left {
    grid-column: 6 span;
    grid-row: 3 span;

    border-radius: 10px;

    @include tablet {
      grid-column: 12 span;
    }
    @include mobile {
      grid-column: 3 span;
      grid-row: 2 span;
    }
  }

  &__right {
    grid-column: 7 / 6 span;
    grid-row: 3 span;

    display: grid;
    grid-template-columns: repeat(6, 1fr);
    grid-template-rows: repeat(3, 170px);
    gap: var(--offset);

    @include tablet {
      grid-column: 12 span;
      grid-template-areas:
      "item5 item5 item5 item6 item6 item6";
    }

    @include mobile {
      grid-column: 3 span;
      grid-template-columns: repeat(3, 1fr);
      grid-template-areas:
      "item5 item5 item5"
      "item6 item6 item6";
    }
  }

  &__top {
    grid-column: 6 span;
    grid-row: 2 span;
    background-color: #B4D1E0;
    border-radius: 10px;
    padding: 35px 35px 50px;

    background:url('./../img/girl.png') no-repeat right 10% bottom, 248px 352px, url('./../img/oval.png') no-repeat right 5% top 50%, 260px 260px, #B4D1E0;

    @include tablet {
      grid-area: "interview";
    }
    @include mobile {
      grid-column: 3 span;
    }
    @media (max-width: 425px) {
      background-image: none;
    }
  }

  &__theme {
    color: #6E99AE;
  }
  &__title {
    font-size: 25px;
    font-family: 'Roboto', sans-serif;
    font-weight: 700;
    line-height: 30px;
    color: #262D33;
    margin-bottom: 15px;

    max-width: 243px;
  }
  &__text {
    font-size: 14px;
    line-height: 20px;
    color: var(--text-color);
    margin-bottom: 25px;
    max-width: 215px;
  }
}
.photographer {
  text-align: center;
  &__title {
    font-size: 40px;
    max-width: 445px;
    font-family: 'Roboto', sans-serif;
    font-weight: 700;
    line-height: 50px;
    text-align: center;
    color: #fff;
    @include mobile {
      font-size: 20px;
      line-height: 25px;
    }
  }
  &__item {
    height: 100%;
    border-radius: 10px;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    background-position: center, center;
    background-size: 555px 570px;
    background-repeat: no-repeat;
    backdrop-filter: blur(10px);
    &--boat {
      background-image: url('./../img/boat.png');
    }
    &--sea {
      background-image: url('./../img/sea.jpg');
    }
    &--night-city {
      background-image: url('./../img/night-city.jpg');
    }
    @include tablet {
      background-size: cover;
    }
  }
  &__name {
    margin: 0;
    font-size: 14px;
    line-height: 20px;
    color: #fff;
  }
  &__prof {
    margin: 0;
    font-size: 12px;
    line-height: 15px;
    color: #fff;
    opacity: .4;
  }
  &__btn {
    padding: 10px 25px 10px 50px;
    background-color: #4592FF;
    color: #fff;
    font-size: 14px;
    line-height: 20px;
    border-radius: 20px;
    background-image: url('./../img/images-icon.svg');
    background-position: left 20px top 50%;
    background-size: 20px 20px;
    background-repeat: no-repeat;
  }
  &__photo {
    border-radius: 50%;
  }
}
.photo-count {
  font-size: 14px;
  line-height: 20px;
  color: #C5E4FC;
}


.swiper {
  height: 100%;
  &-pagination-bullet {
    background-color: #fff;
    opacity: .5;
    &-active {
      opacity: 1;
    }
  }
}
.swiper-horizontal > .swiper-pagination-bullets .swiper-pagination-bullet, .swiper-pagination-horizontal.swiper-pagination-bullets .swiper-pagination-bullet {
  margin: 0 var(--swiper-pagination-bullet-horizontal-gap, 7px);
  margin-bottom: 35px;
}
