.articles {
  padding-top: 30px;
  padding-bottom: 60px;
  &__container {
    display: grid;
    column-gap: var(--offset);
    grid-template-rows: repeat(4, 170px);
    row-gap: var(--offset);


    @include tablet {
      grid-template-columns: repeat(6, 1fr);
      gap: var(--offset);
      grid-template-areas:
      "car car car car item3 item3"
      "car car car car item4 item4"
      "item1 item1 cat cat design design"
      " item2 item2 cat cat design design"
      "aside aside aside aside aside aside";
    }
    @include mobile {
      display: flex;
      flex-wrap: wrap;
    }
  }
}
.article {
  border-radius: 10px;
  background-color: #fff;
  position: relative;
  &__item {
    padding: 25px 25px 30px;
    grid-column: 3 span;
    @include tablet {
      &-1 {
        grid-area: item1;
      }
      &-2 {
        grid-area: item2;
      }
      &-3 {
        grid-area: item3;
      }
      &-4 {
        grid-area: item4;
      }
      &-5 {
        grid-area: item5;
      }
      &-6 {
        grid-area: item6;
      }
    }
    @include mobile {
      display: flex;
      flex-direction: column;
      justify-content: center;
      align-items: center;
    }
    &-title {
      display: inline-block;
      margin: 0;
      margin-bottom: 5px;
      font-size: 16px;
      font-family: 'Roboto', sans-serif;
      font-weight: 700;
      line-height: 25px;
      color: inherit;
      transition: color .2s ease-in, border .2s ease-in;
      &::after {
        content: '';
        position: absolute;
        inset: 0;
      }
      &:focus {
        outline: none;
      }
      @include hover {
        color: var(--accent-color);
      }
      &:active {
        color: var(--active-color);
      }
      margin-bottom: 10px;
    }
    &-date {
      color: #939699;
    }
    @include mobile {
      flex-grow: 1;
    }
  }

  &__top {
    padding-top: 25px;
    padding-left: 35px;
    background-image: url('./../img/car.png');
    background-position: top 50% right;
    background-size: 292px 299px;
    background-repeat: no-repeat;
    position: relative;
    @include mobile {
      background-position: top 50% right -80px;
    }
  }
  &--cars {
    grid-column: 6 span;
    grid-row: 2 span;
    display: flex;
    flex-direction: column;
    justify-content: space-between;

    @include tablet {
      grid-area: car;
    }
    @include mobile {
      flex-grow: 1;
    }
  }

  &--design {
    grid-column: 7 / 3 span;
    grid-row: 2 span;
    padding: 25px;
    background-color: var(--text-color);
    background-image: url('./../img/hand.png');
    background-position: center, center;
    background-size: cover;
    background-repeat: no-repeat;

    display: flex;
    flex-direction: column;
    justify-content: flex-end;
    @include tablet {
      grid-area: design;
    }
  }
  &--cat {
    grid-column: 3 span;
    grid-row: 2 span;
    display: flex;
    flex-direction: column;
    justify-content: flex-end;
    padding: 10px 25px 30px;

    background-image: url('./../img/cat.png');
    background-position: top, left;
    background-size: 263px 275px;
    background-repeat: no-repeat;
    @include tablet {
      grid-area: cat;
      padding: 10px 10px 15px;
      background-size: auto 80%;
    }
    @include mobile {
      flex-grow: 1;
      min-height: 200px;
      background-position: right;
      background-size: auto 100%;
    }
  }
  &__theme {
    display: inline-block;
    position: relative;
    z-index: 3;
    font-size: 12px;
    line-height: 15px;
    letter-spacing: 0.5px;
    text-transform: uppercase;
    transition: color .2s ease-in;
    &:focus {
      outline: none;
    }
    @include hover {
      color: var(--accent-color);
    }
    &:active {
      color: var(--active-color);
    }
    &--cars {
      color: #6E99AE;
      margin-bottom: 20px;
    }
    &--design {
      color: #E7D9FF;
      margin-bottom: 10px;
    }
    &--dest {
      color: var(--accent-color);
      margin-bottom: 25px;

      @include hover {
        color: #6E99AE;
      }
      &:active {
        color: var(--active-color);
      }
    }

  }
  &__title {
    font-family: "Roboto", sans-serif;
    margin: 0;
    display: block;
    color: inherit;

    transition: color .2s ease-in, border .2s ease-in;

    &::after {
      content: '';
      position: absolute;
      inset: 0;
    }
    &:focus {
      outline: none;
    }
    @include hover {
      color: var(--accent-color);
    }
    &--cars {
      max-width: 240px;
      font-size: 25px;
      font-weight: 700;
      line-height: 30px;
      margin-bottom: 25px;
      position: relative;
      z-index: 10;
    }
    &--design {
      color: #fff;
      font-size: 16px;
      line-height: 25px;
      letter-spacing: 0.5px;
      text-transform: uppercase;
    }
  }
  &__text {
    margin: 0;
    max-width: 227px;
    font-size: 14px;
    line-height: 20px;
    color: var(--text-color);
    position: relative;
    z-index: 10;
    &--cat {
      font-family: "Roboto", sans-serif;
      font-size: 16px;
      font-weight: 700;
      line-height: 25px;
    }
  }
  &__comment {
    display: flex;
    align-items: center;
    column-gap: 10px;
    padding: 15px 0;
    border-top: 1px solid #D9DADB;
    padding-left: 35px;
  }

  @include mobile {
    text-align: center;
  }
}


.comment {
  &__text {
    color: var(--text-color);
    font-size: 14px;
    line-height: 20px;
  }
  &__name {
    color: #262D33;
    font-weight: 700;
  }
}
.author {
  display: flex;
  align-items: center;
  column-gap: 10px;
  &__name {
    margin: 0;
    color: #fff;
    font-size: 14px;
    line-height: 20px;
    opacity: .85;
  }

}

.article__title.focus-visible {
  border: 1px solid var(--active-color);
}
.article__theme.focus-visible {
  color: var(--accent-color);
}
