.blogs {
  padding: 30px 0;
  background-color: #262D33;
  @include tablet {
    padding: 15px 0;
  }
  &__list {
    display: flex;
    justify-content: space-between;
    align-items: center;
    padding: 25px 0;
    background-color: #30363D;
    border-radius: 10px;
    @include tablet {
      display: grid;
      grid-template-columns: repeat(12, 1fr);
      gap: var(--offset);
    }

    @include mobile {
      grid-template-columns: repeat(6, 1fr);
      gap: var(--mobile-offset);
      margin: 0 auto;
    }
  }

  &__item {
    display: flex;
    align-items: center;
    justify-content: space-between;
    column-gap: 15px;
    padding: 0 25px;
    &:not(&:last-child) {
      border-right: 1px solid #4B5157;
    }
    @include tablet {
      grid-column: 6 span;
      &:not(&:last-child) {
        border-right: 1px solid #4B5157;
      }
    }
    @include mobile {
      border-right: 1px solid #4B5157;;
    }
  }
  &__link {
    font-size: 16px;
    font-weight: 700;
    line-height: 25px;
    color: #fff;
    transition: color .2s ease-in;
    &:focus {
      outline: none;
    }
    @include hover {
      color: var(--accent-color);
    }
    &:active {
      color: var(--active-color);
    }
  }
}
.blogs__link.focus-visible {
  color: var(--active-color);
}
