.sidebar {

  grid-column: 10 / 3 span;
  grid-row: 4 span;
  border-radius: 10px;
  background-color: #6E99AE;
  max-height: 770px;
  display: flex;
  flex-direction: column;
  @include tablet {
    grid-area: aside;
  }
  @include mobile {
    width: 100%;
    text-align: center;
    order: 10;
  }
  &__title {
    position: relative;
    margin: 0;
    padding: 20px 25px;
    color: #fff;
    font-size: 14px;
    line-height: 20px;
    letter-spacing: 0.5px;
    text-transform: uppercase;
    border-bottom: 1px solid #B4D1E0;
  }
  &__list {
    padding: 0 25px;
  }
  &__item {
    position: relative;
    padding: 45px 0 40px;
    &:not(&:last-child) {
      border-bottom: 1px solid #d9dadb;
    }
    @include tablet {
      padding: 22px 0 20px;
    }
    @include mobile {
      display: none;
    }
  }
  &__link {
    font-size: 20px;
    color: #fff;
    font-family: 'Roboto', sans-serif;
    font-weight: 700;
    line-height: 25px;
    transition: color .2s ease-in-out;
    &::before {
      content: '';
      position: absolute;
      inset: 0;
    }
    &:focus {
      outline: none;
    }
    @include hover {
      color: #4bd0e8;
    }
    &:active {
      color: #d35cdb;
    }
  }
  &__author {
    display: flex;
    align-items: center;
    column-gap: 10px;
    margin-top: 15px;
    &-name {
      margin: 0;
      margin-bottom: 2px;
      font-size: 14px;
      line-height: 20px;
      color: #D1E2EB;
    }
    &-prof {
      margin: 0;
      font-size: 12px;
      line-height: 15px;
      color: #D1E2EB;
    }
    @include mobile {
      justify-content: center;
    }
  }

  &__more {
    display: block;
    text-align: center;
    padding: 11px 10px 14px;
    border-top: 1px solid #B4D1E0;
    font-size: 14px;
    line-height: 20px;
    color: #fff;

    transition: color .2s ease-in-out;
    &:focus {
      outline: none;
    }
    @include hover {
      color: #4bd0e8;
    }
    &:active {
      color: #bb5cdb;
    }
  }
}
.sidebar__link.focus-visible {
  color: var(--active-color);
}
.sidebar__more.focus-visible {
  color: var(--active-color);
}
