.contact {
  &__container {
    padding: 30px 0;
    display: flex;
    justify-content: space-between;
    align-items: center;
    border-top: 1px solid #D9DADB;
    border-bottom: 1px solid #D9DADB;
    @include tablet {
      flex-wrap: wrap;
      flex-direction: column;
      row-gap: 15px;
    }
  }
  &__item {
    &-logo {
      @include mobile {
        display: none;
      }
    }
  }
  &__list {
    display: flex;
    align-items: center;
    column-gap: 30px;
    position: relative;
    @include mobile {
      flex-wrap: wrap;
      justify-content: space-around;
    }
  }
  &__link {
    font-size: 14px;
    font-weight: 700;
    line-height: 20px;
    letter-spacing: 0.5px;
    text-transform: uppercase;

    color: #262D33;

    transition: color .2s ease-in;

    &:focus {
      outline: none;
    }

    @include hover {
      color: var(--accent-color);
    }

    &:active {
      color: var(--active-color);
    }
  }
}
.contact__link.focus-visible {
  color: var(--active-color);
}

.socials {
  &__list {
    display: flex;
    align-items: center;
    column-gap: 15px;
  }
  &__item--logo {
    display: none;
    @include mobile {
      display: block;
    }
  }
  &__link {
    @include hover {
      .socials__link-img--facebook {
        fill: #2564e2;
      }
      .socials__link-img--twitter {
        fill: #0c91e3;
      }
      .socials__link-img--youtube {
        fill: #dd0000;
      }
      .socials__link-img--instagram {
        fill: #0f0e0e;
      }
    }
  }
  &__link-img--facebook {
    fill: #4267B2;
  }
  &__link-img--twitter {
    fill: #1DA1F2;
  }
  &__link-img--youtube {
    fill: #FF0000;
  }
  &__link-img--instagram {
    fill: #262626;
  }
}
