.footer {
  padding-top: 35px;
  padding-bottom: 35px;
  &__container {
    grid-template-columns: repeat(6, 1fr);
    gap: var(--mobile-offset);
  }
  &__nav {
    padding-bottom: 64px;
  }
  &__list {
    grid-column: 2 span;
    &:not(&:last-child) {
      margin-right: calc(var(--offset) * -1);
      padding-right: var(--offset);
    }
    @include mobile {
      grid-column: 3 span;
      &:not(&:last-child) {
        margin-right: unset;
        padding-right: unset;
      }
      
    }
    padding-top: 30px;
    border-top: 5px solid;
    &--news {
      border-top-color: var(--news-color);
    }
    &--arts {
      border-top-color: var(--arts-color);
    }
    &--travel {
      border-top-color: var(--travel-color);
    }
    &--sports {
      border-top-color: var(--sports-color);
    }
    &--tech {
      border-top-color: var(--tech-color);
    }
    &--moneys {
      border-top-color: var(--moneys-color);
    }
  }
  &__item {
    &:not(:last-child) {
      margin-bottom: 15px;
    }
  }
  &__title {
    margin: 0;
    margin-bottom: 25px;
    font-size: 14px;
    font-weight: 700;
    line-height: 20px;
    color: #262D33;
  }
  &__link {
    font-size: 14px;
    line-height: 20px;
    color: var(--text-color);
    transition: color .2s ease-in-out;
    &:focus {
      outline: none;
    }
    @include hover {
      color: var(--accent-color);
    }
    &:active {
      color: var(--active-color);
    }
  }
}
.footer__link.focus-visible {
  color: var(--active-color);
}
