.hero {
  position: relative;
  z-index: 5;
  &::before {
    content: '';
    position: absolute;
    z-index: -1;
    top: 0;
    width: 100%;
    height: 50%;
    left: 0;
    top: 0;
    background-color: #262D33;
  }
  &__container {
    height: 570px;
    @include tablet {
      height: unset;
    }
  }
  &__left {
    height: 100%;
    padding: 55px 55px 100px;
    border-top-left-radius: 10px;
    border-bottom-left-radius: 10px;
    grid-column: 9 span;
    margin-right: -30px;
    background-image: url('./../img/hero-bg.png');
    background-size: cover;
    background-repeat: no-repeat;
    background-position: center center;
    color: #fff;
    display: flex;
    flex-direction: column;
    justify-content: space-between;

    @include tablet {
      grid-column: 12 span;
      margin-right: 0;
      border-top-left-radius: 10px;
      border-top-right-radius: 10px;
      border-bottom-left-radius: unset;
    }
    @include mobile {
      padding: 22px 22px 50px;
    }
  }
  &__right {
    height: 100%;
    border-top-right-radius: 10px;
    border-bottom-right-radius: 10px;
    grid-column: 10 / 3 span;
    background-color: #fff;
    overflow-y: scroll;

    @include tablet {
      grid-column: 12 span;
      overflow-y: visible;
      border-top-right-radius: unset;
      border-bottom-right-radius: unset;
    }
  }
  &__title {
    margin: 0;
    margin-bottom: 45px;
    max-width: 600px;
    font-size: 40px;
    font-weight: 700;
    line-height: 50px;
    @include mobile {
      font-size: 22px;
      line-height: 27px;
    }
  }
  &__btns {
    display: flex;
    align-items: center;
    column-gap: 30px;

    @include mobile {
      flex-direction: column-reverse;
      row-gap: var(--mobile-offset);
      align-items: start;
    }
  }
  &__play {
    display: flex;
    align-items: center;
    column-gap: 10px;
    &-author {
      margin: 0;
      font-size: 14px;
      line-height: 20px;
    }
    &-time {
      margin: 0;
      font-size: 12px;
      line-height: 15px;
      opacity: .4;
      margin-top: 2px;
    }
  }

}

.author-card {
  display: flex;
  align-items: center;
  column-gap: 10px;
  &__name {
    margin: 0;
    color:  #FFF;
    font-size: 14px;
    line-height: 20px;
  }
  &__prof {
    margin: 0;
    opacity: .4;
    color: #fff;
    font-size: 12px;
    line-height: 15px;
  }
}


.hero-recommend {
  &__title {
    margin: 0;
    padding: 20px 0;
    text-align: center;
    font-size: 14px;
    line-height: 20px;
    letter-spacing: 0.5px;
    text-transform: uppercase;
    color: #262D33;
  }

  &__list {
    display: flex;
    flex-direction: column;
    align-items: center;
    overflow-y: scroll;

    @include tablet {
      display: grid;
      align-items: unset;
      grid-template-columns: repeat(12, 1fr);
    }
  }
  &__item {
    padding: 18px 25px;
    border-top: 1px solid #D9DADB;
    position: relative;
    @include tablet {
      grid-column: 6 span;
    }
  }
  &__link {
    color: var(--text-color);
    font-size: 14px;
    line-height: 20px;

    transition: color .2s ease-in-out;
    &::before {
      content: '';
      position: absolute;
      inset: 0;
    }
    &:focus {
      outline: none;
    }
    @include hover {
      color: var(--accent-color);
    }
    &:active {
      color: var(--active-color);
    }
  }
}
.hero-recommend__link.focus-visible {
  color: var(--active-color);
}
.recommend-item {
  position: relative;
  &__title {
    margin: 0;
    margin-bottom: 8px;
    color:  #3BBDC4;
    font-size: 12px;
    line-height: 15px;
    letter-spacing: 0.5px;
    text-transform: uppercase;

    &--cars {
      color: #6E99AE;
    }
    &--movies {
      color: #AC8EE3;
    }
    &--nfl {
      color: #FFA34D;
    }
    &--review {
      color: #3DC47E;
    }
  }
  &:first-child:before {
    content: '';
    position: absolute;
    z-index: 10;
    width: 15px;
    height: 15px;
    background-color: #fff;
    left: 0;
    top: 50%;
    transform: translate(-50%, -50%) rotate(45deg);
  }
}
