html {
  box-sizing: border-box;
}

*,
*::before,
*::after {
  box-sizing: inherit;
}

.page {
  height: 100%;
  font-family: var(--font-family), sans-serif;
  background-color: #EDEFF0;;
  -webkit-text-size-adjust: 100%;
}

.page__body {
  margin: 0;
  min-width: 320px;
  min-height: 100%;
  font-size: 16px;
}

img {
  height: auto;
  max-width: 100%;
  object-fit: cover;
}

a {
  text-decoration: none;
}

.site-container {
  overflow: hidden; // если используете на сайте position: sticky - уберите эту настройку
}

.is-hidden {
  display: none !important; /* stylelint-disable-line declaration-no-important */
}

.btn-reset {
  border: none;
  padding: 0;
  background-color: transparent;
  cursor: pointer;
}

.list-reset {
  list-style: none;
  margin: 0;
  padding: 0;
}

.input-reset {
  -webkit-appearance: none;
  appearance: none;
  border: none;
  border-radius: 0;
  background-color: #fff;

  &::-webkit-search-decoration,
  &::-webkit-search-cancel-button,
  &::-webkit-search-results-button,
  &::-webkit-search-results-decoration {
    display: none;
  }
}

.visually-hidden {
  position: absolute;
  overflow: hidden;
  margin: -1px;
  border: 0;
  padding: 0;
  width: 1px;
  height: 1px;
  clip: rect(0 0 0 0);
}

.container {
  margin: 0 auto;
  padding: 0 var(--container-offset);
  max-width: var(--container-width);

  @include mobile {
    padding: 0 var(--mobile-container-offset);
  }
}

.js-focus-visible :focus:not(.focus-visible) {
  outline: none;
}

.centered {
  text-align: center;
}

.dis-scroll {
  position: fixed;
  left: 0;
  top: 0;
  overflow: hidden;
  width: 100%;
  height: 100vh;
  overscroll-behavior: none;
}

.page--ios .dis-scroll {
  position: relative;
}

.grid {
  display: grid;
  grid-template-columns: repeat(12, 1fr);
  column-gap: var(--offset);

}
.flex {
  display: flex;
}
.btn {
  padding: 10px 45px 10px 25px;
  background-color: #4592ff;
  border-radius: 20px;
  color: #fff;
  font-size: 14px;
  line-height: 20px;
  background-image: url("./../img/arrow.svg");
  background-repeat: no-repeat;
  background-position: top 50% right 20px;
  background-size: 20px 20px;
  transition: background-color .3s ease-in, animation .2s ease-in-out;
  &:focus {
    outline: none;
  }
  @include hover {
    animation: jumping .2s;
    background-color: rgb(43, 195, 255);
  }
  &:active {
    position: relative;
    top: 1px;
  }
}
@keyframes jumping {
  0% {
    background-position: top 50% right 20px;
  }
  50% {
    background-position: top 50% right 10px;
  }
  100% {
    background-position: top 50% right 20px;
  }
}
.date {
  color: #fff;
  font-size: 12px;
  line-height: 15px;
  opacity: .4;
  &--theme {
    color: #939699;
  }
  &--rec {
    color: #939699;
  }
}
.comments-count {
  padding-left: 20px;
  background-image: url('./../img/comment.svg');
  background-position: left, top;
  background-size: 15px 15px;
  background-repeat: no-repeat;
  color: #fff;
  font-size: 12px;
  line-height: 15px;
  opacity: .4;
  &--theme{
    color: #939699;
  }
}
.likes-count {
  padding-left: 20px;
  background-image: url('./../img/heart.svg');
  background-position: left, top;
  background-size: 15px 15px;
  background-repeat: no-repeat;
  color: #fff;
  font-size: 12px;
  line-height: 15px;
  opacity: .4;
  &--theme{
    color: #939699;
  }
}
.menu {
  width: 40px;
  height: 40px;

  &__item {
    height: 5px;
    width: 100%;
    background-color: #fff;
    position: relative;
    &::before {
      content: '';
      height: 5px;
      width: 100%;
      top: -100%;
    }
  }
}
.burger {
  display: none;
  @include tablet {
    @include burger;
    display: block;
    height: 20px;
    z-index: 10;
    color: var(--text-color);
    opacity: .7;
  }
}

.btn-columns {
  width: 10px;
  height: 10px;
  border-top: 3px solid #fff;
  border-right: 3px solid #fff;
  position: absolute;
  right: 50px;
  top: 50%;
  transform: translateY(-50%) rotate(135deg);
}
.visible {
  display: block !important;
}
