.copy {
  padding-top: 40px;
  padding-bottom: 20px;
  align-items: center;
  justify-content: space-between;
  color: var(--text-color);
  @include tablet {
    padding: 20px 0 10px;
    flex-direction: column;
    row-gap: calc(var(--offset) / 2);
    text-align: center;
  }
  &__text {
    margin: 0;
    font-size: 14px;
    line-height: 20px;
    max-width: 750px;
  }
  &__copyright {
    margin: 0;
    font-size: 14px;
    line-height: 20px;
  }
}
