// если вы хотите использовать sass-переменные - удалите root
// colors
:root {
  // base
  --font-family: "Lato", sans-serif;
  --content-width: 1140px;
  --container-offset: 20px;
  --mobile-container-offset: 10px;
  --container-width: calc(var(--content-width) + (var(--container-offset) * 2));
  --mobile-container-width: calc(var(--content-width) + (var(mobile-container-offset) * 2));
  --offset: 30px;
  --mobile-offset: 15px;
  // colors
  --light-color: #fff;
  --accent-color: #3BBDC4;
  --active-color: #AC8EE3;
  --text-color: #4B5157;

  --news-color: #FF4F52;
  --arts-color: #AC8EE3;
  --travel-color: #4592FF;
  --sports-color: #FFA34D;
  --tech-color: #3DC47E;
  --moneys-color: #6E99AE;
}
