.themes {
  padding: 60px 0;

  &__container {
    @include mobile {
      grid-template-columns: repeat(6, 1fr);
      column-gap: var(--mobile-offset);
    }
  }

}
.theme {
  grid-column: 9 span;
  column-gap: 25px;
  padding: 25px;

  position: relative;

  transition: transform .2s ease-in;
  @include hover {
    transform: translateX(-10px);
  }
  &:not(&:last-child) {
    border-bottom: 1px solid #D9DADB;
  }
  &__img {
    width: 336px;
    height: 195px;
    @include mobile {
      height: auto;
      width: 100%;
    }
  }
  &__bookmark {
    position: absolute;
    right: 25px;
    top: 25px;
    &-img {
      fill: #BCBFC2;
      transition: fill .2s ease-in;

      @include mobile {
        fill: #e9e9e9;
      }
    }

    &:hover &-img {
        fill: #EB5757;
    }
    @include mobile {
      right: 30px;
      top: 30px;
    }
  }
  &__title {
    display: block;
    margin: 0;
    margin-top: 15px;
    margin-bottom: 10px;
    font-size: 20px;
    font-family: 'Roboto', sans-serif;
    font-weight: 700;
    line-height: 25px;
    color: #262D33;

    transition: color .2s ease-in;

    &::after {
      content: '';
      position: absolute;
      inset: 0;
    }
    &:focus {
      border: 1px solid (--accent-color);
    }
    @include hover {
      color: var(--accent-color);
    }
    &:active {
      color: var(--active-color);
    }
  }
  &__text {
    color: var(--text-color);
    font-size: 14px;
    line-height: 20px;
  }

  &--flights {
    color: #4592FF;
  }
  &--food {
    color: #4592FF;
  }
  &--science {
    color: #FF4F52;
  }
  &--health {
    color: #3BBDC4;
  }
  &--design {
    color: #AC8EE3;
  }

  @include tablet {
    grid-column: 12 span;
    order: 1;
  }

  @include mobile{
    grid-column: 6 span;
    flex-direction: column;
    order: 1;
  }
}
