.cta {
  text-align: center;
  padding: 60px 0 35px;

  @include mobile {
    padding: 30px 0;
  }
  &__title {
    font-size: 40px;
    font-family: 'Roboto', sans-serif;
    font-weight: 700;
    line-height: 50px;
    color: #262D33;

    @include mobile {
      font-size: 20px;
      line-height: 25px;
    }
  }
  &__form {
    height: 40px;
    margin: 0 auto;
    width: 445px;
    display: flex;
    justify-content: space-between;
    gap: 10px;

    @include mobile {
      width: 100%;
      flex-direction: column;
      align-items: center;
    }
  }
  &__input {
    flex-grow: 1;
    padding: 10px 15px;
    border-radius: 5px;
    border: 1px solid #D9DADB;
    background:  #FFF;
    color: var(--text-color);

    &:focus {
      outline: none;
    }

  }
  &__btn {
    display: inline-block;
    padding: 10px 29px;
    border-radius: 20px;
    background: linear-gradient(to right, #168eff, #83dce6) ;
    background-size: 500% 500%;
    font-size: 14px;
    line-height: 20px;
    color: #fff;

    animation: gradient 5s ease-in-out infinite;
  }
}
.cta__input.focus-visible {
  border: 1px solid #4592FF;
}

@keyframes gradient {
  0% {
    background-position: 0 50%;
  }
  25% {
    background-position: 50% 50%;
  }
  50% {
    background-position: 100% 50%;
  }
  75% {
    background-position: 50% 50%;
  }
  100% {
    background-position: 0 50%;
  }
}
