.header {
  background-color: #fff;
  &__top {
    display: flex;
    justify-content: space-between;
    align-items: stretch;
    margin-top: 5px;
    border-top: 1px solid #D9DADB;

    @include tablet {
      align-items: center;
      column-gap: 15px;
    }

    @include mobile {
      display: grid;
      grid-template-columns: repeat(6, 1fr);
      align-items: center;
      justify-content: space-around;
      gap: var(--mobile-offset);
    }
  }
  &__form {
    display: flex;
    flex-grow: 1;
    padding: 0 15px;
    height: 40px;
    @include mobile {
      grid-column: 3 / 4 span;
      padding: 0;
    }
  }
  &__search-btn {
    margin-right: 10px;
  }
  &__input {
    width: 100%;
    height: 100%;
    font-size: 14px;
    line-height: 20px;
    color: #939699;
    &::placeholder {
      color: inherit;
    }
    &:focus {
      outline: none;
    }

  }
  &__sections-btn {
    margin: 0;
    padding-left: 35px;
    padding-right: 25px;
    border-right: 1px solid #D9DADB;
    height: 40px;

    font-size: 14px;
    line-height: 20px;
    @include mobile {
      grid-column: 2 span;
      display: flex;
      align-items: center;
      justify-content: center;
    }
  }
  &__subscribe {
    position: relative;
    display: flex;
    align-items: center;
    padding-top: 5px;
    margin-right: 25px;
    &-img {
      margin-right: 10px;
    }
    &-title {
      display: inline-block;
      color: #262D33;
      font-size: 12px;
      line-height: 15px;
      transition: color .2s ease-in;
      &::after {
        content: '';
        position: absolute;
        inset: 0;
      }
      &:focus {
        outline: none;
      }
      @include hover {
        color: var(--accent-color);
      }
      &:active {
        color: var(--active-color);
      }
    }
    &-desc {
      margin: 0;
      color: var(--base-grey-50, #939699);
      font-size: 12px;
      line-height: 15px;
    }

    @include mobile {
      grid-column: 3 span;
    }
  }

  &__login {
    display: flex;
    align-items: center;
    column-gap: 10px;

    color: var(--text-color);
    font-size: 14px;
    line-height: 20px;
    transition: color .2s ease-in;
    &:focus {
      outline: none;
    }
    @include hover {
      color: var(--accent-color);
    }
    &:active {
      color: var(--active-color);
    }

    @include mobile {
      grid-column: 4 / 2 span;
    }
  }
.header__link.focus-visible {
  color: var(--active-color);
}

  &__bottom {
    @include mobile {
      grid-template-columns: repeat(6, 1fr);
      gap: var(--mobile-offset);
      padding: var(--mobile-offset) 0;
    }
    &-block {
      grid-column: 4 span;
      align-items: center;
      &--city {
        justify-content: flex-start;
      }
      &--logo {
        justify-content: center;
      }
      &--weather {
        justify-content: flex-end;
      }
      @include mobile {
        grid-column: 6 span;
        justify-content: center;

        .header__statue-img {
          width: 60px;
          height: auto;
        }
      }
    }
  }
  &__city {
    margin: 0;
    margin-left: 15px;
    color: var(--text-color);
    font-size: 14px;
    line-height: 20px;
  }
  &__date {
    color:  var(--text-color);
    font-size: 14px;
    line-height: 20px;
    margin-right: 25px;
  }
  &__weather {
    color:  var(--text-color);
    font-size: 14px;
    line-height: 20px;
    padding-left: 30px;
    background-image: url('./../img/sun.svg');
    background-size: 20px 20px;
    background-repeat: no-repeat;
    background-position: left top;
  }
}
