.rec {
  grid-column: 10 / 3 span;
  grid-row: 4 span;
  max-height: 770px;
  background-color: #fff;
  border-radius: 10px;
  display: flex;
  flex-direction: column;
  &__title {
    margin: 0;
    color: #262D33;
    padding: 20px 25px;
    font-size: 14px;
    line-height: 20px;
    letter-spacing: 0.5px;
    text-transform: uppercase;
    border-bottom: 1px solid #d9dadb;
  }
  &__list {
    padding: 0 25px;
    flex-grow: 1;
    overflow-y: scroll;
  }
  &__item {
    position: relative;
    padding: 17px 0;
    margin-right: -5px;
    column-gap: 10px;
    display: flex;
    align-items: center;
    &:not(&:last-child) {
      border-bottom: 1px solid #d9dadb;
    }
  }
  &__link {
    font-size: 14px;
    line-height: 20px;
    color: var(--text-color);
    transition: color .2s ease-in-out;
    &::before {
      content: '';
      position: absolute;
      inset: 0;
    }
    &:focus {
      outline: none;
    }
    @include hover {
      color: var(--accent-color);
    }
    &:active {
      color: var(--active-color);
    }
  }
  &__time {
    margin: 0;
    margin-top: 5px;
    font-size: 12px;
    line-height: 15px;
    color: #939699;
  }

  &__more {
    display: block;
    padding: 11px 0 14px;
    text-align: center;
    font-size: 14px;
    line-height: 20px;
    color: #262D33;
    border-top: 1px solid #d9dadb;
    font-weight: 700;

    transition: color .2s ease-in-out;
    &:focus {
      outline: none;
    }
    @include hover {
      color: var(--accent-color);
    }
    &:active {
      color: var(--active-color);
    }
  }

  @include tablet {
    grid-column: 12 span;
    max-height: 500px;
    order: 2;
  }
  @include mobile {
    grid-column: 6 span;
    max-height: 300px;
    order: 2;
  }
}
.rec__link.focus-visible {
  color: var(--active-color);
}
.rec__more.focus-visible {
  color: var(--active-color);
}
