.flowers {
  padding: 50px 0 55px;
  // background-color: #000;
  background-image: url('./../img/flowers.jpg');
  background-position: center, center;
  background-size: cover;
  background-repeat: no-repeat;

  display: flex;
  flex-direction: column;
  align-items: center;
  &__title {
    max-width: 640px;
    text-align: center;
    margin: 0;
    margin-bottom: 25px;
    color: #fff;
    font-size: 40px;
    font-family: 'Roboto', sans-serif;
    font-weight: 700;
    line-height: 50px;
    @include mobile {
      font-size: 20px;
      line-height: 25px;
    }
  }
  @include mobile {
    padding: 25px 0;
  }
}
